import React, { useEffect } from "react";
import { Row, Col, Carousel } from "antd";

import {
  Separator,
  DeviceSizeView,
  TwoSidedBlock,
} from "components/common";

import ue_logo from "assets/icons/unreal_engine.svg";
import dl_logo from "assets/icons/deep_learning.svg";

const IconBlock = ({ icon, title, description }) => {
  return (
    <div style={{ textAlign: "center" }}>
      <div>
        <img
          alt="ue logo"
          src={icon}
          style={{ maxWidth: "200px", padding: "10px", margin: "auto" }}
        />
      </div>

      <Separator height={1} />
      <h4>{title}</h4>
      <div style={{ textAlign: "justify" }}>{description}</div>
    </div>
  );
};

const IconBlockCol = ({ icon, title, description }) => {
  return (
    <Col span={12}>
      <IconBlock icon={icon} title={title} description={description} />
    </Col>
  );
};

function Technology() {
  const tech = [
    {
      icon: ue_logo,
      title: "Unreal Engine",
      description: (
        <p>
          Our work pipeline revolves around{" "}
          <a href="https://www.unrealengine.com/">Unreal Engine</a> and its
          extensive ecosystem. As we focus on the creation of digital content,
          Unreal provides an complete suite of tools for world building,
          animation, cinematic rendering and, most importantly, gameplay and
          interactive experiences design.
        </p>
      ),
    },
    {
      icon: dl_logo,
      title: "Deep Learning",
      description: (
        <p>
          As part of our mission we are focused on the pursuit of cutting edge
          technologies to enhance our work pipeline. Thanks to the recent
          success of <b>Deep Learning</b> in many computer vision tasks, we
          believe it coud be the core of many technologies to support content
          creation.
        </p>
      ),
    },
    //{ icon: ue_logo, title: "Unreal Engine", description: "Prova" },
  ];

  useEffect(() => {
    window.scrollTo(0, 0)
  }, [])

  return (
    <>
      <div className="slide slide-first dotted">
        <div className="container">
          <div>
            <h1>Technologies</h1>
            <Separator height={4} />
            <DeviceSizeView screen_size="lg" mode="gt">
              <Row gutter={{ xs: 4, sm: 8, md: 16, lg: 32, xl: 64 }}>
                {tech.length > 0 &&
                  tech.map((el, idx) => {
                    return (
                      <IconBlockCol
                        key={idx}
                        icon={el["icon"]}
                        title={el["title"]}
                        description={el["description"]}
                      />
                    );
                  })}
              </Row>
            </DeviceSizeView>
            <DeviceSizeView screen_size="lg" mode="lt">
              <Carousel autoplay dots={false}>
                {tech.length > 0 &&
                  tech.map((el, idx) => {
                    return (
                      <IconBlock
                        key={idx}
                        icon={el["icon"]}
                        title={el["title"]}
                        description={el["description"]}
                      />
                    );
                  })}
              </Carousel>
            </DeviceSizeView>
          </div>
        </div>
      </div>
      <div className="jumbotron">
        <div className="jumbotron-content">
          <h2>AI for media production</h2>
          <Separator height={1.5} />
          <p style={{ hyphens: "none" }}>
            Bringing the latest advancements in AI technologies to digital creation pipelines.
          </p>
        </div>
      </div>
      <div className="slide dotted">
        <div className="container">
          <div>
            <h1>Our Tools and Solutions</h1>

            <Separator height={2} />
            <TwoSidedBlock
              imageSrc="/img/technology/dl_ue_pytorch.png"
              title="PyTorch for Unreal"
              description="Deep Learning methods are often confined to their research training environments, 
                hardly making to the production stage. To reduce the gap between industry and research,
                we are working to bring the full PyTorch framework inside Unreal, ensuring easy prototyping through blueprints."
              alignment="left"
              use_gradient={false}
              gutter={24}
            />

            <Separator height={2} />
            <TwoSidedBlock
              imageSrc="/img/technology/dl_material.png"
              title="Material Generation"
              description="High quality materials are central to create photorealistic, 
                high quality scenes. For this purpose we develop and trained a custom 
                neural network able to estimate material maps from a single image. 
                Our custom method is able to produce textures up to a 8K resolution 
                with resuced computational requirements."
              alignment="right"
              use_gradient={false}
              gutter={24}
            />
          </div>
        </div>
      </div>
    </>
  );
}

export default Technology;
